html, #root {
    height: 100%;
}
body {
    font-size: 16px;
    box-sizing: border-box;
    min-height: 100%;
    height: 100%;
    width: 100vw;
    overflow-x: hidden;
}
a {
    color: blue;
    text-decoration: none;
}
a.active {
    color: red;
}
#root {
    display: flex;
    flex-direction: column;
    height: 100%;
    width: 100%;
    justify-content: space-between;
}